* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  color: #333;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: #4688F4;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
}

a:hover {
  text-decoration: underline;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 20px 0;
  padding: 0;
}

.secondaryText {
  color: #666;
  font-size: 12px;
}

.heading {
  font-size: 16px;
  font-weight: bold;
}

.ml10 {
  margin-left: 10px;
}

.ml20 {
  margin-left: 20px;
}

.mb5 {
  margin-bottom: 5px;
}

.mb20 {
  margin-bottom: 20px;
}

.mt20 {
  margin-top: 20px;
}

.flex {
  display: -webkit-flex;
  display: flex;
}

.alignItemsCenter {
  -webkit-align-items: center;
          align-items: center;
}

.relative {
  position: relative;
}

.modal {
  background-color: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9;
  -webkit-transition: all 500ms ease-in-out;
  transition: all 500ms ease-in-out;
  -webkit-transform: translateY(calc(-100% - 20px));
          transform: translateY(calc(-100% - 20px));
  padding: 0 20px;
}

.visible {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.headerButton {
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
}

.headerButton:hover {
  text-decoration: underline;
}

.header {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.starsWrapper {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: center;
           justify-content: center;
}

.ratingDescription {
  text-align: center;
  font-size: 18px;
  color: #666;
}

.input {
  font-size: 18px;
  color: #333;
  border: none;
  width: 100%;
  padding: 10px 0;
  resize: none;
}

.input::-webkit-input-placeholder {
  color: #666;
}

.input::-ms-input-placeholder {
  color: #666;
}

.input::placeholder {
  color: #666;
}

.container {
  text-align: left;
}

.generalRatingWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.viewReviewsHeaderWrapper {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  text-align: right;
}

.generalRating {
  background-color: #fed801;
  border-radius: 8px;
  text-align: center;
  font-weight: bold;
  font-size: 16px;
  width: 38px;
  height: 38px;
  line-height: 38px;
}

.avatar {
  width: 38px;
  height: 38px;
  border-radius: 100%;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.username {
  font-weight: 600;
}

.viewReviewsFooterWrapper {
  margin-top: 20px;
  text-align: center;
}

.viewReviewsFooterWrapper a {
  font-weight: bold;
}

.editReview {
  position: absolute;
  top: 0;
  font-size: 35px;
  color: #666;
  right: 0;
  cursor: pointer;
  line-height: 0;
}

.App {
  text-align: left;
  max-width: 500px;
  margin: 0 auto;
  padding: 0 18px;
  position: relative;
}

