.container {
  text-align: left;
}

.generalRatingWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.viewReviewsHeaderWrapper {
  flex-grow: 1;
  text-align: right;
}

.generalRating {
  background-color: #fed801;
  border-radius: 8px;
  text-align: center;
  font-weight: bold;
  font-size: 16px;
  width: 38px;
  height: 38px;
  line-height: 38px;
}

.avatar {
  width: 38px;
  height: 38px;
  border-radius: 100%;
  flex-shrink: 0;
}

.username {
  font-weight: 600;
}

.viewReviewsFooterWrapper {
  margin-top: 20px;
  text-align: center;
}

.viewReviewsFooterWrapper a {
  font-weight: bold;
}

.editReview {
  position: absolute;
  top: 0;
  font-size: 35px;
  color: #666;
  right: 0;
  cursor: pointer;
  line-height: 0;
}
