.modal {
  background-color: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9;
  transition: all 500ms ease-in-out;
  transform: translateY(calc(-100% - 20px));
  padding: 0 20px;
}

.visible {
  transform: translateY(0);
}

.headerButton {
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
}

.headerButton:hover {
  text-decoration: underline;
}

.header {
  align-items: center;
  justify-content: space-between;
}
